.navbar-light .navbar-nav .nav-link{
    color: #dedede;
    }

.navbar-light .navbar-nav .nav-link:hover{
    color: #1bc6d3;
}    

.navbar-light .navbar-nav .nav-link:focus, .navbar-light .navbar-nav .nav-link:hover {
    color: #1bc6d3;
}

.navbar-light .navbar-nav .active>.nav-link, .navbar-light .navbar-nav .nav-link.active, .navbar-light .navbar-nav .nav-link.show, .navbar-light .navbar-nav .show>.nav-link{
    color : #dedede;
}

.navbar-light .navbar-nav .active>.nav-link, .navbar-light .navbar-nav .nav-link.active, .navbar-light .navbar-nav .nav-link.show, .navbar-light .navbar-nav .show>.nav-link:hover
{
    color : #1bc6d3;
}    

.dropdown-menu {
    background-color: #313233 !important;
}

.my-dropdown-item{
    display: block;
    width: 100%;
    padding: .25rem 1.5rem;
    clear: both;
    font-weight: 400;
    color: #212529;
    text-align: inherit;
    white-space: nowrap;
    background-color: transparent;
    border: 0;
}

.my-dropdown-item:hover{
    background-color: #7a7b7c !important;
}

.main-menu-container{
    border-bottom : 1px #f0f0f0 solid;
    font-family: "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
}

@media screen and (max-width: 991px) {
    .main-logo {
        width : 200px;
    }
    .main-menu-container{
        background-color : #24101b
    }
  }

  @media screen and (min-width: 991px) {
    .main-logo {
        width : 400px;
    }

    .main-menu-container{
        background-color : #24101b;
    }
  }

  .navbar-light .navbar-toggler-icon {
    background-image: url("data:image/svg+xml,%3csvg viewBox='0 0 30 30' xmlns='http://www.w3.org/2000/svg'%3e%3cpath stroke='#fff' stroke-width='2' stroke-linecap='round' stroke-miterlimit='10' d='M4 7h22M4 15h22M4 23h22'/%3e%3c/svg%3e");
}

.menu-item{  
    color: #dedede;
    }

.menu-item:hover {  
    color: #1bc6d3;
    }

@media screen and (min-width: 991px) {

    .dropdown>.dropdown-menu {
        top:95%;
        display: none;
        visibility: hidden;
        opacity: 0;
        transition: visibility 0s, opacity 0.3s linear;
    }

    .dropdown:hover>.dropdown-menu {
        display: inline-block;
        visibility: visible;
        opacity: 1;
        transition: visibility 0s, opacity 0.3s linear;
    }

    .dropright>.dropdown-menu {
        display: none;
        visibility: hidden;
        opacity: 0;
        transition: visibility 0s, opacity 0.3s linear;
        left: 95%;
    }

    .dropright:hover>.dropdown-menu {
        display: inline-block;
        visibility: visible;
        opacity: 1;
        transition: visibility 0s, opacity 0.3s linear;
    }

}
