body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Raleway", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image:none;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html, body{
  height: 100%;
}

.Site {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  min-height: 100vh;
}
.Site-content {
  -webkit-flex: 1 0 auto;
          flex: 1 0 auto;
  padding: 1.5em 0 1.5em 0 0;
  width: 100%;
}
.Site-content:after {
  content: ‘\00a0’;
  display: block;
  margin-top: 1.5em 0;
  height: 0;
  visibility: hidden;
}

.Main-Container{
  margin-top: 20px;
  margin-bottom: 30px;
  padding-top: 20px;
  border: 1px #e5e5e5 solid;
  border-radius: 2px;
  padding-bottom:20px;
}

.styled-link{
  text-decoration : none;
  color: #1bc6d3;
}

.header-color{
  color: #1bc6d3;
}

.sub-header-color{
  color: #676767;
}

.wrapper{
  display: -webkit-flex;
  display: flex;  
  
  -webkit-flex-flow: row ;
  flex-flow: row;
  
  text-align: center;
}

.header-left-line{
  -webkit-order: 1;
          order: 1; 
  -webkit-flex: 1 0; 
          flex: 1 0;
}

.header-text{
  -webkit-order: 2;
          order: 2;
  padding-left:20px;
  padding-right:20px;
}

.header-right-line{
  -webkit-order: 3;
          order: 3;
  -webkit-flex: 1 0;
          flex: 1 0;
}

.header-border{
  margin-top:10px;
  border-bottom: 1px solid #1bc6d3;
  border-top: 1px solid #1bc6d3;
  height: 3px;
  font-size: 0px;
  height: 3px;
}

.sub-header-border{
  margin-top:10px;
  border-bottom: 1px solid #676767;
  height: 1px;
  font-size: 0px;
  height: 1px;
}

.content-border{
  margin-top:10px;
  border-bottom: 1px solid #dadada;
  height: 3px;
  font-size: 0px;
  height: 3px;
}

.content-text{
  color: #676767;
  line-height: 1.7;
}

a{
  color: #1bc6d3;
}

a:hover{
  text-decoration: none;
}

.line-item-hover{
  background-color:#eaeaea;
}
.navbar-light .navbar-nav .nav-link{
    color: #dedede;
    }

.navbar-light .navbar-nav .nav-link:hover{
    color: #1bc6d3;
}    

.navbar-light .navbar-nav .nav-link:focus, .navbar-light .navbar-nav .nav-link:hover {
    color: #1bc6d3;
}

.navbar-light .navbar-nav .active>.nav-link, .navbar-light .navbar-nav .nav-link.active, .navbar-light .navbar-nav .nav-link.show, .navbar-light .navbar-nav .show>.nav-link{
    color : #dedede;
}

.navbar-light .navbar-nav .active>.nav-link, .navbar-light .navbar-nav .nav-link.active, .navbar-light .navbar-nav .nav-link.show, .navbar-light .navbar-nav .show>.nav-link:hover
{
    color : #1bc6d3;
}    

.dropdown-menu {
    background-color: #313233 !important;
}

.my-dropdown-item{
    display: block;
    width: 100%;
    padding: .25rem 1.5rem;
    clear: both;
    font-weight: 400;
    color: #212529;
    text-align: inherit;
    white-space: nowrap;
    background-color: transparent;
    border: 0;
}

.my-dropdown-item:hover{
    background-color: #7a7b7c !important;
}

.main-menu-container{
    border-bottom : 1px #f0f0f0 solid;
    font-family: "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
}

@media screen and (max-width: 991px) {
    .main-logo {
        width : 200px;
    }
    .main-menu-container{
        background-color : #24101b
    }
  }

  @media screen and (min-width: 991px) {
    .main-logo {
        width : 400px;
    }

    .main-menu-container{
        background-color : #24101b;
    }
  }

  .navbar-light .navbar-toggler-icon {
    background-image: url("data:image/svg+xml,%3csvg viewBox='0 0 30 30' xmlns='http://www.w3.org/2000/svg'%3e%3cpath stroke='#fff' stroke-width='2' stroke-linecap='round' stroke-miterlimit='10' d='M4 7h22M4 15h22M4 23h22'/%3e%3c/svg%3e");
}

.menu-item{  
    color: #dedede;
    }

.menu-item:hover {  
    color: #1bc6d3;
    }

@media screen and (min-width: 991px) {

    .dropdown>.dropdown-menu {
        top:95%;
        display: none;
        visibility: hidden;
        opacity: 0;
        -webkit-transition: visibility 0s, opacity 0.3s linear;
        transition: visibility 0s, opacity 0.3s linear;
    }

    .dropdown:hover>.dropdown-menu {
        display: inline-block;
        visibility: visible;
        opacity: 1;
        -webkit-transition: visibility 0s, opacity 0.3s linear;
        transition: visibility 0s, opacity 0.3s linear;
    }

    .dropright>.dropdown-menu {
        display: none;
        visibility: hidden;
        opacity: 0;
        -webkit-transition: visibility 0s, opacity 0.3s linear;
        transition: visibility 0s, opacity 0.3s linear;
        left: 95%;
    }

    .dropright:hover>.dropdown-menu {
        display: inline-block;
        visibility: visible;
        opacity: 1;
        -webkit-transition: visibility 0s, opacity 0.3s linear;
        transition: visibility 0s, opacity 0.3s linear;
    }

}

.footer-container{
    height: 50px; 
    background-color: #24101b;
    color : #808080;
    padding-top: 10px;
    display: -webkit-flex;
    display: flex;
    /* align-items: center; */
    -webkit-justify-content: space-between;
            justify-content: space-between;
}

.footer-left {
    padding-left : 10px;
    margin-right: auto;
}

.footer-right {
    padding-right : 10px;
}
.fc-event-time, .fc-event-title {
    padding: 0 1px;
    white-space: nowrap;
    }
 .frame{
  overflow: hidden;
  position:relative;
  cursor: pointer;
  height:182px;
 }

 .hover-image{
  -webkit-transform:scale(1);
          transform:scale(1);
  -webkit-transition: 0.5s ease-in-out;
  -moz-transition: 0.5s ease-in-out;
  -o-transition: 0.5s ease-in-out;
  -webkit-filter: grayscale(0.8);
 }

 .hover-image:hover{
  -webkit-transform:scale(1.2);
          transform:scale(1.2);
  -webkit-transition: 0.5s ease-in-out;
  -moz-transition: 0.5s ease-in-out;
  -o-transition: 0.5s ease-in-out;
  -webkit-filter: grayscale(0);
}

.frame-text{
  pointer-events: none;
  position: absolute;
  z-index: 100;
  height: 30px;
  vertical-align: middle;
  text-align : center;
  color:white;
  font-weight: bold;
  bottom:5%;
  right:5%;
  font-size:1.5em;
  text-shadow: 2px 3px 2px #02080C;
}
