 .frame{
  overflow: hidden;
  position:relative;
  cursor: pointer;
  height:182px;
 }

 .hover-image{
  transform:scale(1);
  -webkit-transition: 0.5s ease-in-out;
  -moz-transition: 0.5s ease-in-out;
  -o-transition: 0.5s ease-in-out;
  -webkit-filter: grayscale(0.8);
 }

 .hover-image:hover{
  transform:scale(1.2);
  -webkit-transition: 0.5s ease-in-out;
  -moz-transition: 0.5s ease-in-out;
  -o-transition: 0.5s ease-in-out;
  -webkit-filter: grayscale(0);
}

.frame-text{
  pointer-events: none;
  position: absolute;
  z-index: 100;
  height: 30px;
  vertical-align: middle;
  text-align : center;
  color:white;
  font-weight: bold;
  bottom:5%;
  right:5%;
  font-size:1.5em;
  text-shadow: 2px 3px 2px #02080C;
}