body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Raleway", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image:none;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html, body{
  height: 100%;
}

.Site {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}
.Site-content {
  flex: 1 0 auto;
  padding: 1.5em 0 1.5em 0 0;
  width: 100%;
}
.Site-content:after {
  content: ‘\00a0’;
  display: block;
  margin-top: 1.5em 0;
  height: 0;
  visibility: hidden;
}

.Main-Container{
  margin-top: 20px;
  margin-bottom: 30px;
  padding-top: 20px;
  border: 1px #e5e5e5 solid;
  border-radius: 2px;
  padding-bottom:20px;
}

.styled-link{
  text-decoration : none;
  color: #1bc6d3;
}

.header-color{
  color: #1bc6d3;
}

.sub-header-color{
  color: #676767;
}

.wrapper{
  display: -webkit-box;
  display: -moz-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;  
  
  -webkit-flex-flow: row ;
  flex-flow: row;
  
  text-align: center;
}

.header-left-line{
  order: 1; 
  flex: 1 0 0;
}

.header-text{
  order: 2;
  padding-left:20px;
  padding-right:20px;
}

.header-right-line{
  order: 3;
  flex: 1 0 0;
}

.header-border{
  margin-top:10px;
  border-bottom: 1px solid #1bc6d3;
  border-top: 1px solid #1bc6d3;
  height: 3px;
  font-size: 0px;
  height: 3px;
}

.sub-header-border{
  margin-top:10px;
  border-bottom: 1px solid #676767;
  height: 1px;
  font-size: 0px;
  height: 1px;
}

.content-border{
  margin-top:10px;
  border-bottom: 1px solid #dadada;
  height: 3px;
  font-size: 0px;
  height: 3px;
}

.content-text{
  color: #676767;
  line-height: 1.7;
}

a{
  color: #1bc6d3;
}

a:hover{
  text-decoration: none;
}

.line-item-hover{
  background-color:#eaeaea;
}